<template>
  <div class="detail">
    <van-nav-bar title="商品详情" left-text="返回" @click-left="goBack" fixed />
    <img class="auto-img bigImg" :src="productData.large_img" alt />
    <div class="detail-list">
      <div class="title clearfix">
        <span class="fl">{{productData.name}}</span>
        <span class="fr" @click="isLikeFn">
          <img
            class="icon auto-img"
            :src="require('../assets/images/'+[isLike ? 'like-active.png':'like.png'])"
            alt="图片未加载..."
          />
        </span>
      </div>
      <div class="classify">
        <div class="list" v-for="(item,index) in productData.result" :key="index">
          <span class="text">{{item.title}}</span>
          <span
            class="option"
            :class="{active:v.isActive}"
            v-for="(v,i) in item.tag"
            :key="i"
            @click="toggleActive(item,v)"
          >{{v.name}}</span>
        </div>
      </div>
      <div class="textBox">
        <div class="text-title">商品描述</div>
        <p v-for="(item,index) in productData.desc" :key="index">{{item}}</p>
      </div>
      <div class="count clearfix">
        <div class="price fl">￥{{productData.price}}</div>
        <div class="fr">
          <van-stepper v-model="value" theme="round" button-size="22" disable-input />
        </div>
      </div>
    </div>
    <van-goods-action>
      <van-goods-action-icon icon="cart-o" text="购物车" :badge="shopCartNum == 0? '':shopCartNum" @click="goShopcart"/>
      <van-goods-action-button color="#be99ff" type="warning" text="加入购物车" @click="addShop" />
      <van-goods-action-button color="#6a5acd" type="danger" text="立即购买" @click="addShop(true)"/>
    </van-goods-action>
  </div>
</template>

<script>
import "../assets/css/detail.less";
export default {
  data() {
    return {
      value: 1,
      isLike: false,
      pid: "",
      productData: {},
      shopCartNum: 0
    };
  },
  created() {
    this.pid = this.$route.query.pid;
    this.getDetailData(this.pid);

    this.findLike(this.pid);

    this.findShop();
  },
  methods: {
    // 跳转页面
    goBack() {
      this.$router.go(-1);
    },
    // 切换口味
    toggleActive(item, v) {
      // 
      // 
      if (v.isActive) {
        return;
      }
      for (let i = 0; i < item.tag.length; i++) {
        if (item.tag[i].isActive) {
          item.tag[i].isActive = false;
          break;
        }
      }
      v.isActive = true;
    },

    // 收藏/取消收藏
    isLikeFn() {
      let tkStr = localStorage.getItem("__Tk");

      let url = this.isLike ? "/notlike" : "/like";
      // 加载提示框
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        // 延迟关闭加载框时间 为0则不自动关闭
        duration: 0
      });
      this.axios({
        method: "post",
        url,
        data: {
          appkey: this.appkey,
          tokenString: tkStr,
          pid: this.pid
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 800 || result.data.code == 900) {
            // 
            this.isLike = result.data.code == 800;
            this.$toast(result.data.msg);
          } else {
            this.$router.push({ name: "Login" });
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 查看当前商品是否被收藏
    findLike(pid) {
      let tkStr = localStorage.getItem("__Tk");
      // 加载提示框
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        // 延迟关闭加载框时间 为0则不自动关闭
        duration: 0
      });
      this.axios({
        method: "get",
        url: "/findlike",
        params: {
          appkey: this.appkey,
          tokenString: tkStr,
          pid: this.pid
        }
      })
        .then(result => {
          this.$toast.clear();
          // 
          if (result.data.code === 1000) {
            this.isLike = result.data.result.length > 0;
          } else {
            this.$router.push({ name: "Login" });
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 加入购物车
    addShop(isPay) {
      let tkStr = localStorage.getItem("__Tk");
      let rules = this.productData.result;
      
      
      let rule = [];
      rules.forEach(v => {
        for (let i = 0; i < v.tag.length; i++) {
          if (v.tag[i].isActive) {
            rule.push(v.tag[i].name);
            break;
          }
        }
      });
      // 
      // 加载框
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "post",
        url: "/addShopcart",
        data: {
          appkey: this.appkey,
          tokenString: tkStr,
          pid: this.pid,
          count: this.value,
          rule: rule.join("/")
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 3000) {
            if (result.data.status == 1) {
              this.shopCartNum++;
            }
            this.$toast(result.data.msg);
            if (isPay == true) {
            setTimeout(() => {
              this.$router.push({name: 'Submit', query: {sids: result.data.sid}});
            }, 400)
          }
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 购物车数量
    findShop() {
      let tkStr = localStorage.getItem("__Tk");

      // 加载框
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "get",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString: tkStr
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 5000) {
            this.shopCartNum = result.data.result.length;
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 跳转到购物车 / 立即购买
    goShopcart(){
      this.$router.push({name:"Shopcart"});
    },
    // 获取商品数据
    getDetailData(pid) {
      this.pid = pid;
      // 加载提示框
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        // 延迟关闭加载框时间 为0则不自动关闭
        duration: 0
      });
      this.axios({
        method: "get",
        url: "http://www.kangliuyong.com:10002/productDetail",
        params: {
          pid,
          appkey: this.appkey
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 600) {
            let data = result.data.result[0];
            data.desc = data.desc.split(/\n/);
            // 
            let productRules = ["cream", "milk", "sugar", "tem"];
            let rules = [];
            productRules.forEach(val => {
              if (data[val] != "") {
                let rule = {
                  title: data[val + "_desc"],
                  tag: []
                };
                let tag = data[val].split(/\//);
                tag.forEach((item, index) => {
                  let t = {
                    name: item,
                    isActive: index == 0
                  };
                  rule.tag.push(t);
                });
                rules.push(rule);
              }
            });
            data.result = rules;
            // 
            this.productData = data;
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
  }
};
</script>

